/* Animation */
@keyframes scale {
    0% {
        transform: scale(1, 1);
    }
    100% {
        transform: scale(1.1, 1.1);
    }
}
@keyframes anima {
    0% {
        background-image: linear-gradient(to right, #aaaaaa, #999999, #888888, #777777);
    }
    25% {
        background-image: linear-gradient(to right, #999999, #888888, #777777, #aaaaaa);
    }
    50% {
        background-image: linear-gradient(to right, #888888, #777777, #aaaaaa, #999999);
    }
    75% {
        background-image: linear-gradient(to right, #777777, #aaaaaa, #999999, #888888);
    }
    100% {
        background-image: linear-gradient(to right, #aaaaaa, #999999, #888888, #777777);
    }
}
.home {
    width: 100%;
    height: 100%;
    .list {
        width: 90%;
        height: 90px;
        background-color: transparent;
        margin: 4% auto;
        display: flex;
        border-radius: 10px;
        border: solid 1px;
        padding: 5px;
        .listDetail {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            input[type="checkbox"] {
                display: none;
            }
            .items {
                width: 50%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-content: center;
                flex-wrap: wrap;
                svg {
                    height: 50px;
                    margin: 0 auto;
                }
                p {
                    font-size: 15px;
                    font-weight: 600;
                    text-align: center;
                    color: #940606;
                }
            }
            .items:hover {
                transform: scale(1.1, 1.1);
                animation: scale 0.5s ease;
            }
            .items:hover svg path {
                fill: #940606;
            }
        }
    }
    .product {
        width: 90%;
        height: 50%;
        display: flex;
        flex-wrap: wrap;
        margin: -4% auto;
        justify-content: space-between;
        .product-info{
            width: 25%;
            height: 350px;
            /* display: inline-block; */
            /* margin-left: 6.3%; */
            /* margin-top: 2%; */
            margin: 2%;
            background: rgba(255, 255, 255, 0.05);
            border-radius: 16px;
            box-shadow: 0 4px 30px rgb(0 0 0 / 10%);
            border: 1px solid rgba(255, 255, 255, 0.2);
            transform: scale(1, 1);
            border-radius: 10px;
            .product-detail {
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
                background-color: transparent;
                .image {
                    width: 100%;
                    height: 30%;
                    img {
                        width: 75%;
                        height: 100%;
                        margin-bottom: 10%;
                        margin-left: 12%;
                        object-fit: contain;
                        mix-blend-mode: color-burn;
                        background-image: none !important;
                    }
                }
                .items {
                    width: 100%;
                    height: 70%;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    .title {
                        width: 100%;
                        height: 10%;
                        p {
                            text-align: center;
                            margin-left: 0%;
                            font-size: 20px;
                            height: 50px;
                            width: 100%;
                            color: #940606;
                        }
                    }
                    .productAccess {
                        width: 100%;
                        height: 36%;
                        .information {
                            padding-left: 3%;
                            p{
                                font-size: 16px;
                            }
                        }
                    }
                    .items-child {
                        width: 100%;
                        height: 21%;
                        display: flex;
                        justify-content: space-between;
                        .money {
                            width: 50%;
                            font-size: 20px;
                            margin-left: 3%;
                            color: #940606;
                        }
                        .button {
                            width: 40%;
                            display: flex;
                            justify-content: flex-start;
                            flex-direction: row-reverse;
                            button {
                                width: 30px;
                                height: 30px;
                                font-size: 16px;
                                border: none;
                                border-radius: 10px;
                                background-color: rgb(70, 117, 200);
                                box-shadow: 2px 2px #444444;
                                margin-right: 5%;
                                svg{
                                    path{
                                        fill:#fff;
                                    }
                                }
                            }
                            button:hover {
                                background-color: rgb(4, 87, 231);
                                text-decoration: underline;
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
        .product-info:hover {
            transform: scale(1.1, 1.1);
            animation: scale 0.5s ease normal;
            opacity: 1;
        }
    }

