    .Access {
        margin: 5%;
        width: 90%;
        height: auto;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: space-around;
        .accDemo {
            min-width:160px;
            width: 21%;
            height: auto;
            margin-top: 5%;
            //animation glassmorphism
            background: rgba(255, 255, 255, 0.05);
            box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            transform: scale(1, 1);
            border-radius: 10px;
            padding-bottom:2%;
            .accDemo_Child {
                width: 100%;
                display: flex;
                flex-direction: column;
            }
            .image {
                width: 100%;
                height: 30%;
                display: flex;
                justify-content: center;
                img {
                    width: 75%;
                    height: 150px;
                    object-fit: contain;
                    mix-blend-mode: color-burn;
                    background-image: none !important;
                }
            }
            .title {
                width: 100%;
                height: 40px;
                p {
                    text-align: center;
                    margin-left: 0%;
                    font-size: 20px;
                    height: 50px;
                    width: 100%;
                    color: #940606;
                }
            }
            .items-child {
                width: 100%;
                height: 21%;
                display: flex;
                justify-content: space-between;
                margin-top: 10%;
                .price {
                    width: 50%;
                    height: 40px;
                    color: #940606;
                    font-size: 20px;
                    font-weight: 500;
                }
                .button {
                    width: 50%;
                    display: flex;
                    justify-content: flex-start;
                    flex-direction: row-reverse;
                    button {
                        width: 30px;
                        height: 35px;
                        font-size: 16px;
                        border: none;
                        border-radius: 10px;
                        background-color: rgb(70, 117, 200);
                        box-shadow: 2px 2px #444444;
                        margin-right: 5%;
                        svg{
                            path{
                                fill:#fff;
                            }
                        }
                    }
                    button:hover {
                        background-color: rgb(4, 87, 231);
                        text-decoration: underline;
                        color: #fff;
                    }
                }
            }
        }
        .accDemo:hover {
            background: rgba(255, 255, 255, 0.15);
            transform: scale(1.1, 1.1);
            animation: scale 0.5s ease normal;
        }
    }
}

